/* ===== EDUCACIÓN SECTION - NUEVO DISEÑO ===== */

.education-section {
  min-height: 100vh;
  background: transparent;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.education-container {
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

/* ===== HEADER ===== */
.education-header {
  text-align: center;
  margin-bottom: 3rem;
}

.education-title {
  font-size: clamp(2.5rem, 5vw, 4rem);
  font-weight: 800;
  color: white;
  margin-bottom: 1rem;
  line-height: 1.1;
  text-shadow: 0 4px 20px rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
}

.education-title-icon {
  width: 3rem;
  height: 3rem;
  color: #fbbf24;
  filter: drop-shadow(0 2px 8px rgba(251, 191, 36, 0.3));
}

.education-subtitle {
  font-size: clamp(1.1rem, 2vw, 1.3rem);
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 2rem;
  font-weight: 400;
  line-height: 1.5;
}

.education-counter {
  display: inline-flex;
  align-items: center;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 30px;
  padding: 0.75rem 1.5rem;
  font-size: 1.2rem;
  font-weight: 700;
  color: white;
  gap: 0.5rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.current-education {
  color: #fbbf24;
  font-size: 1.4rem;
}

.separator {
  opacity: 0.6;
  font-weight: 400;
}

/* ===== NAVIGATION ===== */
.education-navigation {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.nav-button {
  width: 60px;
  height: 60px;
  border: none;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-size: 1.5rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.nav-button:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.25);
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
  border-color: rgba(251, 191, 36, 0.4);
}

.nav-button:disabled {
  opacity: 0.4;
  cursor: not-allowed;
  transform: none;
}

/* ===== CAROUSEL CONTAINER ===== */
.education-carousel-container {
  position: relative;
  width: 100%;
  min-height: 700px;
  border-radius: 24px;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.15);
}

/* ===== EDUCATION CARD ===== */
.education-card {
  padding: 3rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
}

/* Card Header */
.education-card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 2rem;
}

.education-title-section {
  flex: 1;
  min-width: 0;
}

.education-card-title {
  font-size: clamp(2rem, 4vw, 3rem);
  font-weight: 700;
  color: white;
  margin-bottom: 0.75rem;
  line-height: 1.2;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.education-card-institution {
  font-size: clamp(1.1rem, 2vw, 1.3rem);
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
  line-height: 1.4;
}

.education-status-section {
  flex-shrink: 0;
}

.education-status-badge {
  font-size: 0.875rem;
  padding: 0.75rem 1.5rem;
  border-radius: 25px;
  font-weight: 600;
  border: 2px solid;
  backdrop-filter: blur(10px);
}

.education-status-badge.completed {
  background: rgba(34, 197, 94, 0.2);
  color: #4ade80;
  border-color: rgba(34, 197, 94, 0.4);
}

.education-status-badge.in-progress {
  background: rgba(251, 191, 36, 0.2);
  color: #fbbf24;
  border-color: rgba(251, 191, 36, 0.4);
}

/* ===== METADATA GRID ===== */
.education-metadata-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
}

.metadata-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.metadata-item:hover {
  background: rgba(255, 255, 255, 0.12);
  transform: translateY(-2px);
}

.metadata-icon {
  width: 2rem;
  height: 2rem;
  color: #60a5fa;
  flex-shrink: 0;
}

.metadata-label {
  font-size: 0.75rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.6);
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin-bottom: 0.25rem;
}

.metadata-value {
  font-size: 1rem;
  font-weight: 600;
  color: white;
  line-height: 1.3;
}

/* ===== DESCRIPTION ===== */
.education-description {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.education-description p {
  font-size: 1.125rem;
  line-height: 1.7;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 400;
  margin: 0;
}

/* ===== COMPETENCIES ===== */
.education-competencies {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.education-section-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: white;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.section-icon {
  width: 1.75rem;
  height: 1.75rem;
  color: #60a5fa;
}

.competencies-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem;
}

.competency-item {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.08);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.competency-item:hover {
  background: rgba(255, 255, 255, 0.12);
  transform: translateX(4px);
}

.competency-bullet {
  width: 0.75rem;
  height: 0.75rem;
  background: #60a5fa;
  border-radius: 50%;
  margin-top: 0.5rem;
  flex-shrink: 0;
  box-shadow: 0 0 10px rgba(96, 165, 250, 0.3);
}

.competency-item span {
  font-size: 0.95rem;
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.5;
  font-weight: 400;
}

/* ===== CERTIFICATES ===== */
.education-certificates {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.certificates-preview {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}

.certificates-count {
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
}

.view-certificates-btn {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 2rem;
  background: rgba(59, 130, 246, 0.2);
  border: 1px solid rgba(59, 130, 246, 0.3);
  border-radius: 12px;
  color: #60a5fa;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
}

.view-certificates-btn:hover {
  background: rgba(59, 130, 246, 0.3);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  border-color: rgba(59, 130, 246, 0.5);
}

.empty-certificates {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 2rem;
  color: rgba(255, 255, 255, 0.6);
}

.empty-icon {
  width: 3rem;
  height: 3rem;
  margin-bottom: 1rem;
  opacity: 0.5;
}

.empty-certificates p {
  font-size: 1rem;
  margin: 0;
}

/* ===== PROGRESS DOTS ===== */
.education-progress-dots {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 3rem;
}

.progress-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.progress-dot:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.6);
  transform: scale(1.3);
}

.progress-dot.active {
  background: #60a5fa;
  box-shadow: 0 0 15px rgba(96, 165, 250, 0.6);
  transform: scale(1.2);
}

.progress-dot:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* ===== RESPONSIVE DESIGN ===== */
@media (max-width: 1200px) {
  .education-section {
    padding: 1.5rem;
  }
  
  .education-card {
    padding: 2rem;
    gap: 2rem;
  }
  
  .education-card-header {
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .education-status-section {
    align-self: flex-start;
  }
}

@media (max-width: 768px) {
  .education-section {
    padding: 1rem;
  }
  
  .education-header {
    margin-bottom: 2rem;
  }
  
  .education-title {
    flex-direction: column;
    gap: 1rem;
  }
  
  .education-navigation {
    gap: 1rem;
    margin-bottom: 2rem;
  }
  
  .nav-button {
    width: 50px;
    height: 50px;
  }
  
  .education-card {
    padding: 1.5rem;
    gap: 1.5rem;
  }
  
  .education-metadata-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .metadata-item {
    padding: 1rem;
  }
  
  .competencies-grid {
    grid-template-columns: 1fr;
  }
  
  .certificates-preview {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
  
  .education-progress-dots {
    margin-top: 2rem;
  }
}

@media (max-width: 480px) {
  .education-section {
    padding: 0.5rem;
  }
  
  .education-card {
    padding: 1rem;
    gap: 1rem;
  }
  
  .education-card-title {
    font-size: 1.5rem;
  }
  
  .education-card-institution {
    font-size: 1rem;
  }
  
  .education-description,
  .education-competencies,
  .education-certificates {
    padding: 1.5rem;
  }
  
  .education-description p {
    font-size: 1rem;
  }
  
  .competency-item {
    padding: 0.75rem;
  }
  
  .competency-item span {
    font-size: 0.875rem;
  }
  
  .view-certificates-btn {
    padding: 0.75rem 1.5rem;
    font-size: 0.9rem;
  }
}
